		translateText:function(obj, func){
			var texts = new Array();
			var from = translate.language.getLocal();
			var to = translate.language.getCurrent();

			if(typeof(obj) == 'string'){
				//
				texts[0] = obj;
			}else{
				//

				var type = Object.prototype.toString.call(obj);
				//console.log(type);
				if(type == '[object Array]'){
					//
					texts = obj;
				}else if(type == '[object Object]'){
					//
					if(typeof(obj.texts) == 'undefined'){
						console.log('translate.request.translateText  obj.texts   https://translate.zvo.cn/4077.html');	
					}
					if(typeof(obj.texts) == 'string'){
						//
						texts = [obj.texts];
					}else{
						//
						texts = obj.texts;
					}
					if(typeof(obj.from) == 'string' && obj.from.length > 0){
						from = obj.from;
					}
					if(typeof(obj.to) == 'string' && obj.to.length > 0){
						to = obj.to;
					}
				}else{
					console.log('translate.request.translateText  https://translate.zvo.cn/4077.html');
					return;
				}
			}
			

			var url = translate.request.api.translate;
			var data = {
				from:from,
				to: to,
				text:encodeURIComponent(JSON.stringify(texts))
			};
			//console.log(data);
			translate.request.post(url, data, function(data){
				//console.log(data); 
				if(data.result == 0){
					console.log('=======ERROR START=======');
					console.log('from : '+data.from);
					console.log('to : '+data.to);
					console.log('translate text array : '+texts);
					console.log('response : '+data.info);
					console.log('=======ERROR END  =======');
					//return;
				}

				func(data);
			}, null);
		},
		listener:{
			minIntervalTime:800, // 800 200
			lasttime:0,//  translate.execute() 13
			/*
				13
				0
				200
			*/
			executetime:0,
			/*
				
				ajax translate.execute() 
			*/
			delayExecuteTime:200, 
			/*
				ajax
				11010
			*/
			addExecute:function(){
				var currentTime = Date.now();
				if(translate.request.listener.lasttime == 0){
					//lasttime
					translate.request.listener.executetime = currentTime;
					translate.request.listener.lasttime = 1;
				}else{
					//

					if(translate.request.listener.executetime > 1){
						//
						//console.log(' '+currentTime);
					}else{
						//

						if(currentTime < translate.request.listener.lasttime + translate.request.listener.minIntervalTime){
							//++
							translate.request.listener.executetime = translate.request.listener.lasttime + translate.request.listener.minIntervalTime;
							//console.log('addexecute - < ++');
						}else{
							translate.request.listener.executetime = currentTime;
							//console.log('addexecute -- OK ');
						}
					}
					

				}

				
			},
			/*
				
				url ajaxurlurl200
				 return true; urltrue translate.execute; ,urlfalseurl
			*/
			trigger:function(url){
				return true;
			},
			/*
				ajax
				
			*/
			start:function(){
				
				//
				if(typeof(translate.request.listener.isStart) != 'undefined'){
					return;
				}else{
					translate.request.listener.isStart = true;
				}

				//100
				setInterval(function(){
					var currentTime = Date.now();
					if(translate.request.listener.executetime > 1 && currentTime > translate.request.listener.executetime+translate.request.listener.delayExecuteTime){
						translate.request.listener.executetime = 0;
						translate.request.listener.lasttime = currentTime;
						try{
							//console.log(' --'+currentTime);
							translate.execute();
						}catch(e){
							console.log(e);
						}
					}
				}, 100);

				const observer = new PerformanceObserver((list) => {
					var translateExecute = false;	// true 
				    for(var e = 0; e < list.getEntries().length; e++){
				    	var entry = list.getEntries()[e];

				    	if (entry.initiatorType === 'fetch' || entry.initiatorType === 'xmlhttprequest') {
				        	var url = entry.name;
				        	//console.log(url);
				        	//urltranslate.js
				        	if(typeof(translate.request.api.host) == 'string'){
				        		translate.request.api.host = [translate.request.api.host];
				        	}
				        	var ignoreUrl = false; // url true

				        	//translate.service 
				        	for(var i = 0; i < translate.request.api.host.length; i++){
				        		if(url.indexOf(translate.request.api.host[i]) > -1){
				        			//
				        			ignoreUrl = true;
				        			break;
				        		}
				        	}
				        	//client.edge 
				        	if(url.indexOf(translate.service.edge.api.auth) > -1){
				        		ignoreUrl = true;
				        	}
				        	if(url.indexOf('.microsofttranslator.com/translate') > -1){
				        		ignoreUrl = true;
				        	}

				        	if(ignoreUrl){
				        		//console.log(''+url);
								continue;
				        	}
				        	if(translate.request.listener.trigger()){
				        		//
				        	}else{
				        		//
				        		continue;
				        	}

				        	translateExecute = true;
				        	break;
				        }
				    }
				    if(translateExecute){
				    	//console.log('translate.request.listener.addExecute() -- '+Date.now());
				    	translate.request.listener.addExecute();
				    }
				});
				// observer"resource"
				observer.observe({ type: "resource", buffered: true });
			}
		}
